*,
*::after,
*::before{
  margin: 0;
  padding:0;
  box-sizing: inherit;
}
li {
  list-style: none;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: #ffffff;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}
 body {
   background-color: blueviolet;
   color: rgb(230, 255, 105);
   margin-top: 2rem;
  font-size: 2rem;
 }
 

